  padding: 0 1em 0 1em;
}
footer {
  margin: 2.25em 0 0 0;
}
/* Box */
.box {
  background: #ffffff;
  border-bottom: solid 1px #ccc;
  padding: 2.75em 1.75em 2.75em 1.75em;
}
.box .image.featured {
  position: relative;
  width: auto;
  left: 1.75em;
  top: 1.75em;
  margin: -4.5em 0 4.25em -3.5em;
}
.box.post header {
  padding-top: 1em;
  margin: 0 0 2em 0;
}
.box.post h2 {
  font-size: 2.5em;
  letter-spacing: -0.015em;
}
.box.post header > p {
  margin-top: 1.25em;
  font-size: 1.25em;
}
.box footer ul.actions {
  margin-bottom: 0;
}
/* Button */
input[type="button"],
input[type="submit"],
input[type="reset"],
button,
.button {
  -webkit-appearance: none;
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
  border: 0;
  border-radius: 5px;
  background: #d52349;
  color: #fff !important;
  font-weight: 700;
  outline: 0;
  font-size: 1.1em;
  padding: 0.65em 1.5em 0.65em 1.5em;
  text-align: center;
  -webkit-transition: background-color .25s ease-in-out;
  transition: background-color .25s ease-in-out;
}
input[type="button"]:hover,
input[type="submit"]:hover,
input[type="reset"]:hover,
button:hover,
.button:hover {
  background: #e53359;
}
input[type="button"]:active,
input[type="submit"]:active,
input[type="reset"]:active,
button:active,
.button:active {
  background: #c51349;
}
input[type="button"].alt,
input[type="submit"].alt,
input[type="reset"].alt,
button.alt,
.button.alt {
  background: #252122;
}
input[type="button"].alt:hover,
input[type="submit"].alt:hover,
input[type="reset"].alt:hover,
button.alt:hover,
.button.alt:hover {
  background: #353132;
}
input[type="button"].alt:active,
input[type="submit"].alt:active,
input[type="reset"].alt:active,
button.alt:active,
.button.alt:active {
  background: #151112;
}
input[type="button"].icon:before,
input[type="submit"].icon:before,
input[type="reset"].icon:before,
button.icon:before,
.button.icon:before {
  opacity: 0.35;
  position: relative;
  top: 0.05em;
  margin-right: 0.75em;
}
input[type="button"].large,
input[type="submit"].large,
input[type="reset"].large,
button.large,
.button.large {
  font-size: 1.5em;
  padding: 0.75em 1.5em 0.75em 1.5em;
}
/* Image */
.image {
  display: inline-block;
  outline: 0;
}
.image img {
  display: block;
  width: 100%;
}
.image.centered {
  display: block;
  margin: 0 0 2em 0;
}
.image.centered img {
  margin: 0 auto;
  width: auto;
}
.image.featured {
  display: block;
  width: 100%;
  margin: 0 0 2em 0;
}
.image.left {
  float: left;
  margin: 0 2em 2em 0;
}
/* List */
ul {
  list-style: disc;
  padding-left: 1em;
}
ul li {
  padding-left: 0.5em;
}
ol {
  list-style: decimal;
  padding-left: 1.25em;
}
ol li {
  padding-left: 0.25em;
}
/* Actions */
ul.actions {
  list-style: none;
  padding-left: 0;
}
ul.actions li {
  display: inline-block;
  margin-left: 1em;
  padding-left: 0;
}
ul.actions li:first-child {
  margin-left: 0;
}
/* Links */
ul.links {
  list-style: none;
  padding-left: 0;
}
ul.links li {
  display: inline-block;
  border-left: solid 1px rgba(255, 255, 255, 0.05);
  padding: 0 0 0 1em;
  margin: 0 0 0 1em;
}
ul.links li:first-child {
  border-left: 0;
  padding-left: 0;
  margin-left: 0;
}
/* Contact */
ul.contact {
  list-style: none;
  padding-left: 0;
}
ul.contact li {
  position: relative;
  border-top: solid 1px #ddd;
  padding: 1.3em 0 1.3em 7em;
}
ul.contact li h3 {
  position: absolute;
  left: 0;
  top: 1.3em;
  font-size: 1.1em;
}
ul.contact li p {
  margin: 0;
}
ul.contact li:first-child {
  padding-top: 0;
  border-top: 0;
}
ul.contact li:first-child h3 {
  top: 0;
}
/* Divided */
ul.divided {
  list-style: none;
  padding-left: 0;
}
ul.divided li {
  border-top: solid 1px #ddd;
  padding: 1em 0 1em 0;
}
ul.divided li:first-child {
  border-top: 0;
  padding-top: 0;
}
/* Social */
ul.social {
  list-style: none;
  padding-left: 0;
  overflow: hidden;
  cursor: default;
  margin: 0 0 3.25em 0;
}
ul.social li {
  display: inline-block;
  padding-left: 0;
  margin-left: 0.75em;
}
ul.social li:first-child {
  margin-left: 0;
}
ul.social li a {
  display: inline-block;
  width: 3em;
  height: 3em;
  text-align: center;
  border-radius: 5px;
  background: #888;
  -webkit-transition: background-color .25s ease-in-out;
  transition: background-color .25s ease-in-out;
}
ul.social li a:before {
  color: #fff;
  font-size: 2em;
  line-height: 1.5em;
}
ul.social li a.fa-facebook {
  background: #3c5a98;
}
ul.social li a.fa-facebook:hover {
  background: #4c6aa8;
}
ul.social li a.fa-twitter {
  background: #2daae4;
}
ul.social li a.fa-twitter:hover {
  background: #3dbaf4;
}
ul.social li a.fa-dribbble {
  background: #c4376b;
}
ul.social li a.fa-dribbble:hover {
  background: #d4477b;
}
ul.social li a.fa-linkedin {
  background: #006599;
}
ul.social li a.fa-linkedin:hover {
  background: #1075a9;
}
ul.social li a.fa-tumblr {
  background: #2b4661;
}
ul.social li a.fa-tumblr:hover {
  background: #3b5671;
}
ul.social li a.fa-google-plus {
  background: #da2713;
}
ul.social li a.fa-google-plus:hover {
  background: #ea3723;
}
/* Icons */
.icon {
  position: relative;
  text-decoration: none;
}
.icon:before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  text-transform: none !important;
}
.icon > .label {
  display: none;
}
.icon.featured {
  position: relative;
  display: inline-block;
  background-color: #d52349;
  width: 9em;
  padding: 1.75em 0 0.75em 0;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  margin: 0 0 4.5em 0;
  cursor: default;
}
.icon.featured:before {
  font-size: 4.5em;
  line-height: 1em;
  color: #fff;
}
.icon.featured:after {
  content: '';
  position: absolute;
  bottom: -1.95em;
  left: 0;
  border-top: solid 2em #d52349;
  border-left: solid 4.5em transparent;
  border-right: solid 4.5em transparent;
}
.icon.featured.alt {
  background-color: #252122;
}
.icon.featured.alt:after {
  border-top-color: #252122;
}
.icon.featured.alt2 {
  background-color: #827a7c;
}
.icon.featured.alt2:after {
  border-top-color: #827a7c;
}
/* Page Wrapper */
#page-wrapper > section {
  margin-bottom: 0;
}
/* Header */
#header {
  position: relative;
  background: #ffffff;
  border-bottom: solid 1px #ccc;
  padding: 5em 0 4em 0;
  text-align: center;
}
#header h1 {
  color:#d52349;
  font-weight: 900;
  font-size: 2.5em;
  letter-spacing: -0.035em;
  line-height: 1;
}
#header img{
  position:relative;
  top:32px;
  width:90px;
  height: 90px;
  left:16px;
}
/* Nav */
#nav {
  margin: 2.5em 0 0 0;
}
#nav > ul {
  margin: 0;
}
#nav > ul > li {
  display: inline-block;
  font-style: italic;
  margin: 0 0.35em 0 0.35em;
}
#nav > ul > li > ul {
  display: none;
}
#nav > ul > li > a {
  border-radius: 5px;
  color: #5d5d5d;
  text-decoration: none;
  padding: 0.6em 1.2em 0.6em 1.2em;
  -webkit-transition: background-color .25s ease-in-out;
  transition: background-color .25s ease-in-out;
  outline: 0;
}
#nav > ul > li:hover > a, #nav > ul > li.active > a {
  background: #d52349;
  color: #fff !important;
  font-weight: 700;
}
/* Banner */
#banner {
  width:100% !important;
  background:url("https://drive.google.com/uc?id=1upSMBqULEWHgo0vOmKVdPVLJTnid5Twf");
  background-position: center;
  background-size:cover;
  background-repeat:no-repeat;
  -webkit-clip-path: polygon(50% 0%, 100% 0, 100% 75%, 50% 100%, 0% 75%, 0 0);
  clip-path: polygon(50% 0%, 100% 0, 100% 75%, 50% 100%, 0% 75%, 0 0);  text-align: center;
  position: relative;
  padding: 4em 0 9em 0;
  margin: 4em 0 0 0;
}
#banner header {
  background-color: rgba(34, 30, 31, 0.98);
  display: inline-block;
  padding: 2.5em 5em;
  border-radius: 5px;
}
#banner header h2 {
  color: #fff;
  font-weight: 700;
  font-size: 2.5em;
  margin: 0 0 0.65em 0;
}
#banner header p {
  color: #d52349;
  padding: 0;
  font-style: normal;
  margin: 0;
  font-size: 1.5em;
}
/* Intro */
#intro {
  text-align: center;
  overflow: hidden;
}
#intro section {
  margin: 3em 0;
  padding: 2.5em 0;
}
#intro h2 {
  font-size: 1.75em;
}
#intro p {
  margin: 0;
}
#intro .middle {
  position: relative;
  z-index: 1;
}
#intro .middle:before {
  content: '';
  width: 32px;
  height: 100%;
  position: absolute;
  left: -24px;
  top: 0;
  display: block;
  z-index: -1;
  -webkit-box-shadow: 32px 0 0 0 #fff, 0 -32px 0 0 #fff, 0 32px 0 0 #fff, 32px 32px 0 0 #fff, 32px -32px 0 0 #fff, 0 0 32px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 32px 0 0 0 #fff, 0 -32px 0 0 #fff, 0 32px 0 0 #fff, 32px 32px 0 0 #fff, 32px -32px 0 0 #fff, 0 0 32px 0 rgba(0, 0, 0, 0.15);
}
#intro .middle:after {
  content: '';
  width: 32px;
  height: 100%;
  position: absolute;
  right: -24px;
  top: 0;
  display: block;
  z-index: -1;
  -webkit-box-shadow: -32px 0 0 0 #fff, 0 -32px 0 0 #fff, 0 32px 0 0 #fff, -32px 32px 0 0 #fff, -32px -32px 0 0 #fff, 0 0 32px 0 rgba(0, 0, 0, 0.15);
          box-shadow: -32px 0 0 0 #fff, 0 -32px 0 0 #fff, 0 32px 0 0 #fff, -32px 32px 0 0 #fff, -32px -32px 0 0 #fff, 0 0 32px 0 rgba(0, 0, 0, 0.15);
}
#intro .button {
  min-width: 12em;
}
#intro footer {
  margin: 0;
}
/* Main */
#main {
  position: relative;
  background: #fff;
  padding: 4em 0 4em 0;
}
#main:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-size: 100% 100%;
  z-index: 0;
  opacity: 0.1;
}
#main .major h2 {
  /*background: #f7f7f7 url("..");*/
}
/* Footer */
#footer {
  position: relative;
  color: #858484;
  padding: 4em 0;
}
#footer:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  /*background: url("images/bg01.png");*/
  background-size: 100% 100%;
  z-index: 0;
  opacity: 0.5;
}
#footer a {
  color: #c5c4c4;
}
#footer h2, #footer h3, #footer h4, #footer h5, #footer h6, #footer strong, #footer b {
