    -moz-osx-font-smoothing: grayscale;
    content: '\F0C9';
    display: block;
    position: absolute;
    left: 10px;
    top: 10px;
    width: 50px;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    color: #fff;
    text-align: center;
    background: rgba(150, 150, 150, 0.75);
    border-radius: 5px;
  }

  #titleBar .toggle:active:before {
