  #intro section {
    padding: 40px 0 0 0;
    margin: 20px 0 0 0;
  }

  #intro h2 {
    font-size: 1.25em;
  }

  /* Banner */

  #banner {
    padding: 6em 0;
    margin: 2em 0 0 0;
  }

  #banner header {
    padding: 1.5em 1em 1.5em 1em;
    display: block;
    border-radius: 0;
  }

  #banner header h2 {
    font-size: 1.5em;
    line-height: 1.5em;
    margin: 0 0 0.25em 0;
  }

  #banner header p {
    font-size: 1.2em;
  }

  /* Main */

  #main {
    padding: 20px 0;
  }

  /* Footer */

  #footer {
    padding: 40px 0 20px 0;
  }

}
