  background-color:#FAFAFA;
  position:relative;
  border-radius: 6px;
  overflow: auto;
  font-family:Serif;
  color:rgb(213, 35, 73);
  font-weight:normal;
}
.shell i{
   position:absolute;
   right:15px;
   top:22px;
}
